<template>
  <div>
    <h5>This is the char selection profile!</h5>
    <div>
      <img :src="baseUrl + 'profile/' + char.ID + '.jpg'">
      <h3>{{char.Name}}</h3>
      <p>{{char.Profile}}</p>
    </div>
    <div>
      <span v-for="(s, key) in skillset" :key='key'>
        <img :src="baseUrl + 'skill/' + s.Main.skill_key + '.jpg'">
      </span>
    </div>
    <button @click="addToTeam()">Add to Team</button>
  </div>
</template>

<script>
import { OneSkillSet } from "@/services/Fetch.js"
export default {
  props: {
    char: {},
    skillset: {},
  },
  data(){
    return {
      baseUrl: "/img/character/"
    }
  },
  methods:{
    addToTeam(){
      if (this.char.ID !== undefined) {
        this.$store.dispatch("user/addToTeamSelection", this.char)
      }
    } 
  },
  watch:{
    char(){
      OneSkillSet(this.char.ID)
      .then(res => {
        this.skillset = res.data
      })
      .catch(err => {
        alert(err)
      })
    }
  }
};
</script>