<template>
  <div>
    <div>{{teamSelected}}</div>
    <button @click="startGame()">Start game</button>
  </div>
</template>

<script>
export default {
  computed: {
    teamSelected: function() {
      return this.$store.getters["user/TeamSelection"];
    }
  },
  methods: {
    startGame() {
      const teamId = this.$store.getters['user/TeamSelectionId']

      if (teamId.length === 3 ){
        const userId = this.$store.getters["user/ID"]

        const payload = { userId: userId, teamId: teamId }
        this.$store.dispatch("socket/connect", payload);
      }
    }
  },
  created() {
    const that = this
    const unwatch = this.$store.watch(
      () => this.$store.getters["socket/code"],
      code => {
        if (code === 0 || code === 3){
          console.log("initial update")
          that.$router.push({name: "ingame"})
          unwatch()
        }
      }
    );
  }
};
</script> 