<template>
<div>
   <h5>Char select list</h5>
   <button @click="toIngame()">to ingame</button>
   <section>
      <div v-for="(char, key) in roster" :key='key' @click="selectChar(char)">
         <img :src="baseURL + char.ID + '.jpg'" :alt="char.Name">
      </div>
   </section>
   <CharSelectProfile :char="selected"/>
   <TeamSelection/>
</div>
</template>

<script>

import CharSelectProfile from "@/views/components/layout/CharSelectProfile.vue"
import TeamSelection from "@/views/components/ui/teamSelected.vue"

import { PersonaAll } from "@/services/Fetch.js"

export default {
   components:{
      CharSelectProfile,
      TeamSelection
   },
   data(){
      return {
         baseURL: "/img/character/profile/",
         roster : null,
         selected: {}
      }
   },
   methods:{
      selectChar(char){
         this.selected = char 
      },
      toIngame(){
         this.$router.push({name:"ingame"})
      }
   },
   created(){
      PersonaAll()
         .then(res => {
            this.roster = res.data
         })      
         .catch(err => {
            alert(err)
         })
   }
};
</script>
